body {
  font-family: "Noto Sans KR", sans-serif;
  font-size: 11pt;
  text-align: left;
  margin: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -10; }
  body * {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
  body a, body a:visited {
    color: inherit;
    text-decoration: underline; }
  body span {
    font-size: 11pt;
    font-weight: 400; }
  body [data-visible="opaque"] {
    opacity: 1; }
  body [data-visible="translucent"] {
    opacity: 0.4; }
  body [data-visible="transparent"] {
    opacity: 0; }

.wrapper {
  padding: 15vh 5vw;
  min-width: calc(90vw - $lndr-scrollbar-approx);
  min-height: 70vh; }

.header {
  margin-bottom: 2rem;
  display: inline-block;
  z-index: 10; }
  .header h1 {
    font-size: calc(33pt + 2vw);
    margin: 0 0 .5rem 0;
    font-weight: 700; }
  .header h2 {
    font-size: calc(16.5pt + 1vw);
    margin: 0 0 .5rem 0;
    font-weight: 500; }
  .header span {
    color: #b5897f; }

.main {
  display: block;
  z-index: 10; }
  .main [data-white-space="preserved"] {
    white-space: pre; }

.footer {
  display: contents; }
  .footer .blhx {
    position: fixed;
    z-index: -5;
    bottom: 10vh;
    right: 5vw;
    max-width: 50vw;
    height: auto;
    max-height: 70vh;
    transition: opacity .4s ease-in-out; }
